<template>
  <div>home
    <el-upload
      action="http-request"
      :http-request="onUpload"
      list-type="picture-card"
      :on-preview="handlePictureCardPreview"
      :on-remove="handleRemove">
      <i class="el-icon-plus"></i>
    </el-upload>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt="">
    </el-dialog>
  </div>
</template>

<script>
import ImageCompress from 'tm-image-compress'

// const imageCompress = new ImageCompress()

export default {
  name: 'home',

  data() {
    return {
      dialogImageUrl: '',
      dialogVisible: false
    }
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, fileList)
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    async onUpload (params) {
      console.log(params.file.size)
      const imageCompress = await new ImageCompress(params.file)
      console.log('imageCompress:', imageCompress.file.size)
    },
  }
}
</script>

